.offer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:4rem 0;
}

.offer h2 {
    font-size: 44px;
    margin-bottom: 2rem;
    text-align: center;
}

.offerCard{
    display: flex;
    flex-wrap:wrap;
    justify-content: space-evenly;
}

.offerCard>div {
    box-shadow: 0px 15px 30px rgba(83, 83, 83, 0.15);
    width: 18rem;
    padding:2rem 1rem;
    text-align: center;
    margin: 1rem;
}

.offerCard h4 {
    padding-bottom:1rem;
}



